@font-face {
  font-family: '-Regular';
  src: url("/fonts/______-Regular.ttf");
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: '-Medium';
  src: url("/fonts/______-Medium.ttf");
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: '-Bold';
  src: url("/fonts/______-Bold.ttf");
  font-weight: 900;
  font-style: normal;
}
@font-face {
  font-family: '-Italic';
  src: url("/fonts/______-Italic.ttf");
  font-weight: normal;
  font-style: normal;
}
body {
  background-color: #b28af8;
  min-height: 100vh;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}
*,
*:before,
*:after {
  box-sizing: border-box;
}
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
ul[class],
ol[class],
li {
  list-style: none;
}
ul[class],
ol[class] {
  padding: 0;
}
a:not([class]) {
  text-decoration-skip-ink: auto;
}
img {
  border-radius: 2em;
  max-width: 100%;
  display: block;
}
input,
button,
textarea,
select {
  font: inherit;
}
h1 {
  color: #fff;
  font-size: 5em;
}
h2 {
  color: #fff;
  margin: 0;
}
h3,
h4 {
  color: #fff;
}
p {
  color: #fff;
  margin: 0;
}
a {
  color: #000;
  text-decoration: none;
}
nav {
  color: #fff;
}
.section {
  background-color: #d290ff;
  padding: 10em 0;
}
.container {
  width: 100%;
  max-width: 1170px;
  padding: 0 15px;
  margin: 0 auto;
  overflow: hidden;
}
.title {
  margin-bottom: 2em;
  text-align: center;
  font-size: 34px;
  font-weight: 700;
}
.subtitle {
  text-align: center;
}
.header {
  background-color: #d290ff;
  padding-top: 1rem;
}
.header-inner {
  display: flex;
  justify-content: space-between;
}
.header-inner .header-nav .header-ul {
  display: flex;
  justify-content: space-between;
}
.header-inner .header-nav .header-ul .header-li {
  margin-left: 5px;
}
.header-inner .btn-light {
  border-radius: 1em;
}
.main-inner {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.main-inner .main-left .main-h1 {
  margin: 0.2em 0;
}
.main-inner .main-left .main-p {
  margin: 3em 0;
}
.main-inner .main-right {
  display: grid;
  justify-items: right;
}
.about-inner .about-info {
  display: grid;
  grid-template-columns: 1fr 1fr;
}
.about-inner .about-info .about-right {
  display: grid;
  justify-items: left;
  line-height: 20px;
}
.about-inner .about-info .about-right .about-p {
  width: 25em;
}
.about-inner .about-info .about-right .btn-light {
  border-radius: 2em;
  width: 12em;
  height: 3em;
  margin-top: 3em;
}
.services-inner .services-content .services-items {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  justify-items: center;
  background-color: #956ae1;
  border-radius: 9em;
  padding: 2em;
}
.services-inner .services-content .services-items .services-item .services-h3 {
  font-weight: 700;
  font-size: 6em;
}
.services-inner .services-content .services-items .services-item .services-p {
  text-align: center;
  font-size: 1.5em;
}
.cards-bgc {
  background-color: #956ae1;
}
.cards-bgc .cards .cards-paragraph {
  display: grid;
}
.cards-bgc .cards .grid-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 25px;
}
.cards-bgc .cards .grid-container .card-item {
  padding: 50px 20px;
  background-color: #d290ff;
  border-radius: 1em;
}
.portfolio-bgc {
  background-color: #956ae1;
}
.portfolio-bgc .portfolio .portfolio-inner .portfolio-slider {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2em;
}
.portfolio-bgc .portfolio .portfolio-inner .portfolio-slider .portfolio-item {
  background-color: #b28af8;
  border-radius: 2em;
}
.portfolio-bgc .portfolio .portfolio-inner .portfolio-slider .portfolio-item .portfolio-img {
  margin-bottom: 1em;
}
.portfolio-bgc .portfolio .portfolio-inner .portfolio-slider .portfolio-item .portfolio-p {
  line-height: 2em;
  text-align: center;
  margin: 2em 0;
}
.blogs-bgc {
  background-color: #956ae1;
}
.blogs-bgc .blogs .blogs-inner .blogs-slider {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 2em;
}
.blogs-bgc .blogs .blogs-inner .blogs-slider .blogs-item {
  background-color: #b28af8;
  border-radius: 2em;
}
.blogs-bgc .blogs .blogs-inner .blogs-slider .blogs-item .blogs-img {
  margin-bottom: 1em;
}
.blogs-bgc .blogs .blogs-inner .blogs-slider .blogs-item .blogs-p {
  line-height: 2em;
  margin: 2em;
}
.footer {
  margin: 10em 0;
}
.footer-inner .footer-description .footer-img {
  display: block;
  margin: 0 auto;
}
.footer-inner .footer-bottom {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 3em;
  margin: 5em 0;
}
.footer-inner .footer-bottom .footer-item .item-aspect-1 {
  display: flex;
  margin-top: 1em;
}
.footer-inner .footer-bottom .footer-item .item-aspect-1 .item-row-0 {
  margin-right: 5em;
}
.footer-inner .footer-bottom .footer-item .footer-detail {
  margin-bottom: 1em;
}
.footer-inner .footer-link {
  display: flex;
  justify-content: space-between;
}
.footer-inner .footer-link .footer-soc {
  display: flex;
}
.footer-inner .footer-link .footer-soc .soc-img {
  margin-right: 10px;
  width: 30px;
  height: 30px;
}
